/* src/App.css */

/* --- REGLAS GLOBALES --- */
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f0f8ff !important; /* Tu regla de fondo global */
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

/* Estilos generales para el contenedor principal de la aplicación */
.app-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh; /* Asegura que la aplicación ocupe al menos toda la altura de la vista */
    overflow-x: hidden !important; /* Previene scroll horizontal no deseado */
    position: relative;
    z-index: 1;
}

/* --- ESTILOS PARA EL ENCABEZADO (HEADER) --- */
header {
    background-color: #FFD700 !important; /* ¡AMARILLO PARA EL HEADER! */
    color: #333 !important; /* Texto oscuro para contrastar con el amarillo */
    padding: 15px 20px; /* Ajustado a un padding uniforme */
    display: flex; /* Para alinear el logo y la navegación */
    justify-content: space-between; /* Espacio entre el logo y la nav */
    align-items: center; /* Centra verticalmente los elementos */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Sombra ligera */
    position: sticky; /* Para que el header se quede arriba al hacer scroll */
    top: 0;
    width: 100%;
    z-index: 1000; /* Asegura que esté por encima de otros contenidos */
    box-sizing: border-box; /* IMPORTANTE: Asegura que el padding no añada al ancho total */
}

.logo {
    font-size: 1.8em;
    font-weight: bold;
    color: #333 !important; /* Asegura que el logo también sea oscuro */
    margin-right: auto; /* Empuja el logo a la izquierda */
    padding-left: 5px; /* Pequeño padding para que no se pegue al borde izquierdo */
}

nav {
    padding-right: 5px; /* Pequeño padding para que el último elemento no se pegue al borde derecho */
}

nav ul {
    list-style: none; /* Elimina las viñetas de la lista */
    padding: 0;
    margin: 0;
    display: flex; /* Para que los ítems del menú estén en fila */
    gap: 25px; /* Espacio entre los ítems del menú */
}

nav ul li a {
    color: #333 !important; /* Texto oscuro para los enlaces */
    text-decoration: none; /* Elimina el subrayado de los enlaces */
    font-size: 1.1em;
    font-weight: 600; /* Un poco más de negrita */
    transition: color 0.3s ease; /* Transición suave al pasar el ratón */
    padding: 5px 0; /* Pequeño padding para mejorar el área clicable */
    white-space: nowrap; /* Evita que el texto "Inicio" se corte en varias líneas */
}

nav ul li a:hover {
    color: #8B4513 !important; /* Un color marrón suave al pasar el ratón */
}

/* Estilos para el contenido principal (Main) */
main {
    flex-grow: 1; /* Esto hace que el contenido principal ocupe todo el espacio disponible entre el header y el footer */
}

/* Estilos para el pie de página (Footer) */
footer {
    background-color: #333; /* Fondo oscuro para el footer */
    color: white;
    text-align: center;
    padding: 20px;
    font-size: 0.9em;
    width: 100%; /* Asegura que el footer ocupe todo el ancho */
    margin-top: auto; /* Empuja el footer al final si el contenido es corto */
    box-sizing: border-box; /* Asegura que el padding no añada al ancho total */
}

footer p {
    margin: 0; /* Elimina el margen predeterminado del párrafo en el footer */
}

/* --- Media Queries para responsividad del APP.CSS --- */
@media (max-width: 768px) {
    header {
        flex-direction: column; /* Apila el logo y la navegación en móviles */
        padding: 10px 15px; /* Ajustado para móviles */
        align-items: flex-start; /* Alinea el logo a la izquierda en vista de columna */
    }

    .logo {
        margin-bottom: 10px; /* Espacio entre el logo y el menú apilado */
        margin-right: 0; /* Elimina margin-right auto en móvil */
    }

    nav {
        width: 100%; /* La navegación ocupa todo el ancho */
        padding-right: 0; /* Elimina padding-right en móvil */
    }

    nav ul {
        flex-direction: column; /* Apila los ítems del menú */
        width: 100%; /* Los ítems de la lista también ocupan todo el ancho */
        gap: 5px; /* Reduce el espacio entre ítems en móvil */
        text-align: left; /* Alinea el texto del menú a la izquierda en móvil */
    }

    nav ul li {
        width: 100%; /* Cada ítem de la lista ocupa todo el ancho */
    }

    nav ul li a {
        display: block; /* Hace que el enlace ocupe todo el ancho de su li */
        padding: 8px 10px; /* Ajusta el padding para un mejor área de clic */
        text-align: center; /* Centra el texto del enlace en móvil */
    }
}