/* src/components/SobreNosotros.css */

/* Estilos para la sección principal de "Sobre Nosotros" */
.seccion-sobre-nosotros {
    padding: 40px 20px; /* Espacio alrededor del contenido */
    max-width: 1200px; /* Ancho máximo para centrar el contenido */
    margin: 20px auto; /* Centra la sección en la página */
    background-color: #fff; /* Fondo blanco para el recuadro */
    border-radius: 8px; /* Bordes redondeados */
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1); /* Sombra suave */
    text-align: center; /* Centra el título y la línea */
}

/* Título principal "Sobre Nosotros" */
.titulo-seccion-snack {
    color: #FFD700; /* Amarillo como en tu diseño */
    font-size: 2.5em;
    margin-bottom: 10px;
}

/* Línea decorativa debajo del título */
.linea-decorativa-amarilla {
    border: none;
    border-top: 3px solid #FFD700; /* Línea amarilla */
    width: 80px; /* Ancho de la línea */
    margin: 0 auto 30px auto; /* Centra la línea y da espacio abajo */
}

/* Contenedor GRID para las dos columnas */
.contenido-nosotros-grid {
    display: grid;
    /* Define dos columnas: la primera para la imagen (1 parte), la segunda para el texto (2 partes) */
    grid-template-columns: 1fr 2fr;
    gap: 40px; /* Espacio entre las columnas */
    align-items: center; /* Centra verticalmente el contenido de las columnas */
    text-align: left; /* Alinea el texto a la izquierda dentro de sus columnas */
    padding: 20px;
}

/* Columna de la imagen */
.imagen-nosotros-columna {
    display: flex; /* Para centrar la imagen si es más pequeña */
    justify-content: center;
    align-items: center;
}

.imagen-tostilocos {
    max-width: 100%; /* La imagen no excederá el ancho de su columna */
    height: auto; /* Mantiene la proporción de la imagen */
    border-radius: 8px; /* Opcional: bordes redondeados para la imagen */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Sombra para la imagen */
}

/* Columna del texto de la historia */
.texto-historia-columna {
    /* No se necesitan estilos complejos aquí, ya que el grid lo maneja */
}

.subtitulo-historia {
    color: #333; /* Color oscuro para el subtítulo */
    font-size: 1.8em;
    margin-bottom: 15px;
    font-weight: 600;
}

.texto-historia-columna p {
    line-height: 1.7; /* Espaciado entre líneas para mejor legibilidad */
    margin-bottom: 15px;
    color: #555; /* Un gris más suave para el texto del párrafo */
    font-size: 1.05em;
}

/* Estilo para la frase final */
.frase-final-nosotros {
    font-weight: bold;
    color: #FFD700; /* Amarillo para la frase final */
    font-size: 1.1em;
    margin-top: 25px;
}

/* Media Queries para responsividad (para pantallas más pequeñas) */
@media (max-width: 768px) {
    .contenido-nosotros-grid {
        grid-template-columns: 1fr; /* Cambia a una sola columna en pantallas pequeñas */
        gap: 20px; /* Reduce el espacio */
        text-align: center; /* Centra el texto y la imagen */
    }

    .texto-historia-columna {
        padding: 0; /* Elimina padding adicional en móvil */
    }

    .titulo-seccion-snack {
        font-size: 2em; /* Ajusta el tamaño del título */
    }

    .subtitulo-historia {
        font-size: 1.5em; /* Ajusta el tamaño del subtítulo */
    }
}